

/*Navbar*/
.navbar {
    background-color:  #4B286D;
    font-family: 'Poppins', sans-serif;
    color: white !important;
}

.nav-link, nav-item{
    color: white;
}


/* .navbar li:hover {
    background-color:  #b9e0c9;
    color: white;   
} */

.dropdown-item{
    color: black !important;
}


/*Homepage*/
.homepage{
   padding: 1%;
   background-color: #111312;
   height: 100vh;
}

p.welcome {
    font-size: 45px;
    text-align: center;
    padding-top: 15%;
    font-weight: bold;
    color: white;
    font-family: 'IBM Plex Sans', sans-serif; 
}

p.content {
    font-size: 20px;
    padding-top: 1%;
    color: white;
    font-family: 'IBM Plex Sans', sans-serif;
    text-align: center;
}

.homepage img{
   float: right;
   margin-top: 4%;
   margin-right: -8.5%;
}


/*Weeks*/
p.title{
    font-family: 'IBM Plex Sans', sans-serif;
    font-weight: bold;
    font-size: 70px;
    text-align: left;
}

p{
    font-family: 'IBM Plex Sans', sans-serif;
    font-size: 20px;
    padding-top: 5%;
    display: inline;
}

.saturday{
    padding: 10%;
}

.figure{
    width: 45%;
    float: right;
    border: 2px solid red;
    
}

.img{
    width: 25%;
    float: right;
    padding: 1%;
}






/*Sunday*/
.sunday img{
  display: block;
  margin: auto;
}

.figure img{
    width: 50%;
    margin: auto;
    display: block;
}


/*Footer*/
.page-footer{
    background-color: #7aa659;
}

a{
    text-decoration: none;
    color: white;
    cursor: pointer;
}


a:hover{

    color: yellow;
    cursor: pointer;
}

/*mix*/
.card mix{
    border:5px solid #95D1CC;
}